/* ==========================================================================
   Blog List shortcode global style - begin
   ========================================================================== */

.edgt-blog-list-holder {
	@include edgtRelativeHolderLayout();
	clear: both;
	
	.edgt-blog-list {
		@include edgtRelativeHolderLayout();
		list-style: none;
		margin: 0;
		padding: 0;
	}
	
	.edgt-bl-item {
		@include edgtRelativeHolderLayout();
		padding: 0;
		margin: 0;
		box-sizing: border-box;
	}
	
	.edgt-bli-inner {
		@include edgtRelativeHolderLayout();
	}
	
	.edgt-bli-content {
		@include edgtRelativeHolderLayout();
		box-sizing: border-box;
	}
	
	.edgt-post-image {
		@include edgtRelativeHolderLayout();
		margin: 0 0 30px;
		overflow: hidden;
		
		img {
			display: block;
            width: 100%;
			@include edgtTransform(translateZ(0));
			@include edgtTransition(transform .5s ease);
			backface-visibility: hidden;
		}
		
		a {
			display: block;
		}

		&:hover {
			img {
				@include edgtTransform(scale(1.03));
			}
		}
	}
	
	.edgt-bli-info {
		@include edgtRelativeHolderLayout();
		margin: 0 0 5px;
		clear: both;
		
		> div {
			position: relative;
			display: inline-block;
			vertical-align: top;
			font-size: 14px;
			font-weight: 400;
			line-height: 1.2;
			float: left;
			padding: 0 20px 5px 0;
			color: #888;
			
			&:last-child {
				padding: 0 0 5px;
			}
			
			a {
				color: #888;
				
				&:hover {
					color: #000;
				}
			}
		}
	}
	
	.edgt-post-title {
		margin: 0;
	}

	.edgt-post-info-author-avatar-holder {
		overflow: hidden;
		display: inline-block;
		width: 30px;
		height: 30px;
		vertical-align: middle;
		margin-right: 7px;
		-webkit-border-radius: 50%;
		-moz-border-radius: 50%;
		border-radius: 50%;
	}

    .edgt-post-info-author-text,
    .edgt-post-info-author-link{
        color: #b9b9b9!important;
    }
	
	.edgt-post-excerpt {
		@include edgtRelativeHolderLayout();
		margin: 14px 0 0;
		
		p {
			margin: 0;
		}
		
		&:after {
			content: '...';
		}
	}
	
	.edgt-post-read-more-button {
		@include edgtRelativeHolderLayout();
		margin: 12px 0 0;
	}
	
	/***** Columns Space style - begin *****/
	
	$columns_space_label: ('no', 'tiny', 'small', 'normal');
	$columns_space: (0, 5, 10, 15);
	
	@for $i from 0 to length($columns_space_label) {
		&.edgt-bl-#{nth($columns_space_label,$i+1)}-space {
			$column_width: nth($columns_space, $i+1);
			$column_width_margin: $column_width * 2;
			
			.edgt-bl-wrapper {
				@if($column_width == 0) {
					margin: 0;
				} @else {
					margin: 0 -#{$column_width}px;
				}
			}
			
			.edgt-bl-item {
				padding: 0 #{$column_width}px;
				margin: 0 0 #{$column_width_margin}px;
			}
		}
	}
	
	/***** Columns Space style - end *****/
}
/* ==========================================================================
   Blog List shortcode global style - end
   ========================================================================== */