/* ==========================================================================
   Blog List shortcode - Simple type style - begin
   ========================================================================== */

.edgt-blog-list-holder {
    
    &.edgt-bl-simple {

        .edgt-bl-item{
            margin: 0 0 48px;
        }
        
        .edgt-post-image {
            display: table-cell;
            vertical-align: top;
            width: 136px;
        }
        
        .edgt-bli-content {
            display: table-cell;
            vertical-align: top;
            width: auto;

            .edgt-post-excerpt {
                margin-top: 12px;
            }

            .edgt-bli-info {
                margin-bottom: 0;
            }
            
            .edgt-post-info-date,
            .edgt-post-info-comments-holder {
                display: block;
                margin: 20px 0 0;
                line-height: 1;
            }

            .edgt-post-info-date{
                margin: 16px 0 0;
            }

            &:not(:first-child) {
                padding-left: 20px;
            }
        }
    }
}
/* ==========================================================================
   Blog List shortcode - Simple type style - end
   ========================================================================== */