/* Single posts navigation styles - begin
    ====================================================================== */

.edgt-blog-single-navigation {
    position: relative;
    display: table;
    width: 100%;
    vertical-align: middle;
    padding: 0;
    clear: both;
    box-sizing: border-box;
    border-top: 1px solid #e1e1e1;

    .edgt-blog-single-navigation-inner {
        margin: 34px 0 33px;
    }

    .edgt-blog-single-prev,
    .edgt-blog-single-next {

        color: $default-text-color;

        &:hover{
            .edgt-nav-blog-post-label-wrapper {
                .edgt-blog-single-nav-label,
                .edgt-blog-single-nav-title {
                    color: $first-main-color;
                }
            }
        }

        .edgt-blog-single-nav-mark {
            font-size: 48px;
            line-height: inherit;
            color: inherit;

            &:before {
                display: block;
                line-height: inherit;
            }
        }

        .edgt-blog-single-nav-label {
            font-size: 14px;
            color: $default-heading-color;
            font-weight: 600;
        }

        div {
            display: table-cell;
            vertical-align: middle;
        }

        .edgt-blog-single-nav-title {
            margin: 0;
            font-size: 14px;
            font-weight: 400;
            color: $default-text-color;
            font-family: $default-text-font;
        }
    }

    .edgt-blog-single-prev {
        float: left;

        .edgt-blog-single-nav-mark,
        .edgt-blog-single-nav-label {

        }

        .edgt-blog-single-nav-mark {
            margin: 0 14px 0 0;
        }

        div {
            padding-right: 25px;
        }
    }

    .edgt-blog-single-next {
        float: right;

        .edgt-blog-single-nav-mark,
        .edgt-blog-single-nav-label {

        }

        .edgt-blog-single-nav-mark {
            margin: 0 0 0 14px;
        }

        div {
            float: right;
            padding-left: 25px;
            text-align: right;
        }

        .edgt-nav-blog-post-label-wrapper {
            position: absolute;
            right: 103px;
            top: 50%;
            @include edgtTransform(translateY(-50%));
        }
        &.edgt-no-nav-image {
            .edgt-nav-blog-post-label-wrapper {
                right: 0;
            }
        }
    }

    .edgt-nav-image {
        display: block;
        width: 78px;
        height: 78px;
    }
}

.edgt-single-links-pages {
    clear: both;
    margin: 20px 0 5px;

    .edgt-single-links-pages-inner {

        > a,
        > span {
            position: relative;
            display: inline-block;
            vertical-align: middle;
            margin: 0 15px 5px 0;
        }
    }
}
/* Single posts navigation styles - end
    ====================================================================== */