/* ==========================================================================
   Blog List - Masonry style - begin
   ========================================================================== */

.edgt-blog-holder {
	
    &.edgt-blog-masonry {

        article {
            margin: 0 0 20px;
            padding: 0 0 30px;

            &.edgt-post-has-media {
                .edgt-post-heading {
                    margin-bottom: 24px;
                }
            }

            .edgt-post-title {
                margin: 0 0 5px 0;
            }

            .edgt-post-excerpt-holder {
                @include edgtRelativeHolderLayout();
                margin: 0 0 19px;
            }

            .edgt-more-link-container {
                @include edgtRelativeHolderLayout();
                margin: 20px 0 0;
            }

            .edgt-post-info-top {
                margin-bottom: 8px;

                > div {
                    position: relative;
                    display: inline-block;
                    padding-right: 8px;
                    margin-right: 3px;
                    font-weight: 600;
                    font-size: 14px;
                    color: #b9b9b9;

                    a {
                        color: inherit;

                        &:hover {
                            color: #606264;
                        }
                    }

                    &:last-child {
                        padding-right: 0;
                        margin-right: 0;
                    }

                    &.edgt-post-info-category {
                        a:before {
                            content: "\e017";
                            font-family: 'ElegantIcons';
                            display: inline-block;
                            margin-right: 6px;
                            position: relative;
                            top: 2px;
                        }
                        a:hover {
                            &:before {
                                color: #606264;
                            }
                        }
                    }
                }
            }

            .edgt-post-info-bottom {
	            margin-top: 0;
	            padding-top: 15px;
	            border-top: 1px solid #dadada;

                .edgt-post-info-bottom-left,
                .edgt-post-info-bottom-right {
	                position: relative;
	                display: inline-block;
	                width: 50%;
	                float: left;
                    line-height: 25px;
                }

                .edgt-post-info-bottom-right {
	                
                    > div {
                        margin-left: 15px;
	                    
	                    &:first-child {
		                    margin-left: 0;
	                    }
                    }
                }

                .edgt-post-info-author {
                    display: inline-block;
	                
                    a {
                        text-transform: capitalize;
                        color: #b9b9b9;
	                    
                        &:hover {
                            color: #606264;
                        }
                    }
                }

                .edgt-post-info-bottom-right {
                    text-align: right;
                }

                .edgt-post-info-comments-holder,
                .edgt-blog-like {
                    display: inline-block;

                    a {
                        display: inline-block;

                        span,
                        i {
                            vertical-align: middle;
                            color: inherit;
                        }
                    }
                }

                .edgt-blog-like {
	                
                    i:first-child,
                    span:first-child {
                        @include edgtTransition(color 0.15s ease-out);
                        margin-right: 7px;
                        color: #c4c3c3;
                    }

                    &:hover {
	                    
                        i:first-child,
                        span:first-child {
                            color: #606264;
                        }
                    }
                }

                .edgt-post-info-comments-holder {
	                
                    span:first-child {
                        @include edgtTransition(color 0.15s ease-out);
                        margin-right: 3px;
                        color: #c4c3c3;
                    }
	                
                    &:hover {
	                    
                        span:first-child {
                            color: #606264;
                        }
                    }
                }

                > div {
                    position: relative;
                    display: inline-block;
                    padding-right: 8px;
                    margin-right: 3px;
                    font-weight: 600;
                    font-size: 14px;
                    color: #b9b9b9;

                    a {
                        color: inherit;

                        &:hover {
                            color: #606264;
                        }
                    }

                    &:last-child {
                        padding-right: 0;
                        margin-right: 0;
                    }

                    &.edgt-post-info-category {
                        a:before {
                            content: "\e07c";
                            font-family: 'ElegantIcons';
                            display: inline-block;
                            margin-right: 6px;
                            position: relative;
                            top: 2px;
                        }
                        a:hover {
                            &:before {
                                color: #606264;
                            }
                        }
                    }
                }

                .edgt-post-info-author-avatar-holder {
                    overflow: hidden;
                    display: inline-block;
                    width: 30px;
                    height: 30px;
                    vertical-align: middle;
                    margin-right: 13px;
                    -webkit-border-radius: 50%;
                    -moz-border-radius: 50%;
                    border-radius: 50%;
                }
            }

            &.format-audio {
                &.edgt-post-no-media.has-post-thumbnail {
                    .edgt-post-heading {
                        margin-bottom: 30px;
                    }
                }
            }

            &.format-link {
	            
                .edgt-post-text {
	                padding: 48px 25px 41px 40px;
                    overflow: hidden;
                    background-color: #f9f9f9;
                }
	            
                .edgt-post-text-main {
                    display: table;
                    table-layout: fixed;
                    height: 100%;
                    width: 100%;
                    text-align: left;
                }

                .edgt-post-mark {
                    position: absolute;
                    top: 10px;
                    right: 10px;
                    font-size: 75px;
                    color: #b9b9b9;
                    opacity: 0.22;

                    .edgt-link-mark {
                        padding-top: 5px;
                    }
                }

                .edgt-post-info-bottom {
                    border: none;
                    padding-top: 10px;
                }
	            
                .edgt-post-link-holder {
                    @include edgtTableCellLayout();
                    vertical-align: top;
                    text-align: left;
                }
	            
                .edgt-post-title {
                    margin-bottom: 0;
                }
            }
	        
            &.format-quote {
	            
                .edgt-post-text {
	                padding: 46px 25px 32px 40px;
                    overflow: hidden;
                    background-color: #f9f9f9;
                }
	            
                .edgt-post-text-main {
                    display: table;
                    table-layout: fixed;
                    height: 100%;
                    width: 100%;
                    text-align: left;
                }

                .edgt-post-mark {
                    position: absolute;
                    top: 10px;
                    right: 10px;
                    font-size: 75px;
                    color: #b9b9b9;
                    opacity: 0.22;

                    .edgt-quote-mark {
                        padding-top: 5px;
                    }
                }

                .edgt-quote-author {
                    font-weight: 600;
                    font-size: 14px;
                    color: #b9b9b9;
                }

                .edgt-post-info-bottom {
                    border: none;
                    padding-top: 23px;
                }
	            
                .edgt-post-link-holder {
                    @include edgtTableCellLayout();
                    vertical-align: top;
                    text-align: left;
                }
	            
                .edgt-post-title {
                    margin-bottom: 0;
                }
	            
                .edgt-post-author {
	                display: inline-block;
	                margin-bottom: 23px;
	                font-size: 13px;
	                line-height: 20px;
                }
            }
        }

        .edgt-blog-pagination {
            margin: 47px 0 52px;
        }
    }
}
/* ==========================================================================
   Blog List - Masonry style - end
   ========================================================================== */