/* ==========================================================================
   Blog Single style - begin
   ========================================================================== */

.edgt-blog-holder.edgt-blog-single {

	.edgt-comment-form {
		margin-bottom: 0;
	}
	
	article {
		margin: 0 0 30px;
		
		.edgt-post-heading {
			margin-bottom: 28px;
		}
		
		.edgt-post-text {
			padding: 0 0 21px;
		}
		
		.edgt-post-info-top {
			margin: 14px 0 21px;
			
			> div {
				position: relative;
				display: inline-block;
				padding-right: 8px;
				margin-right: 3px;
				font-weight: 600;
				font-size: 14px;
				color: #b9b9b9;

				a {
					color: inherit;

					&:hover {
						color: #606264;
					}
				}
				
				&:last-child {
					padding-right: 0;
					margin-right: 0;
				}

				&.edgt-post-info-category {
					a:before {
						content: "\e017";
						font-family: 'ElegantIcons';
						display: inline-block;
						margin-right: 6px;
						position: relative;
						top: 1px;
                        @include edgtTransition(color .2s ease-out);
					}

					a:hover {
						&:before {
							color: #606264;
						}
					}
				}

				&.edgt-blog-like {
					.fa-heart-o {
						margin-right: 4px;
					}
				}
			}
		}
		
		.edgt-post-info-bottom {
			@include edgtRelativeHolderLayout();
			margin: 38px 0 0;
			padding: 16px 0 0;
			border-top: 1px solid #ebebeb;
			
			.edgt-post-info-bottom-left,
			.edgt-post-info-bottom-right {
				position: relative;
				display: inline-block;
				width: 50%;
				float: left;
			}
			
			.edgt-post-info-bottom-left {
                line-height: 1;
				> div {
					position: relative;
					display: inline-block;
					padding-right: 8px;
					margin-right: 3px;
					color: $default-text-color;
					
					a {
						display: inline-block;
						color: #000;
						text-transform: uppercase;
						font-weight: 600;
						font-size: 12px;
						line-height: 20px;
						margin: 0 12px 5px 0;
						
						&:hover {
							color: $first-main-color;
						}
					}
					
					&:after {
						content: "/";
						position: absolute;
						top: 0;
						right: -2px;
					}
					
					&:last-child {
						padding-right: 0;
						margin-right: 0;
						
						&:after {
							content: '';
						}
					}
				}
			}
			
			.edgt-post-info-bottom-right {
				text-align: right;
				
				> div {
					margin-left: 17px;
					
					&:first-child {
						margin-left: 0;
					}
				}
			}
		}

		&.format-link {
			.edgt-post-text{
				padding: 40px 42px 20px;
                background-color: #f9f9f9;
			}

			.edgt-post-text-main {
				display: table;
				table-layout: fixed;
				height: 100%;
				width: 100%;
				text-align: left;
			}

			.edgt-post-mark {
				position: absolute;
				top: 8px;
				right: 30px;
				font-size: 125px;
				color: #b9b9b9;
				opacity: 0.22;

				.edgt-link-mark {
					padding-top: 5px;
				}
			}

			.edgt-post-link-holder {
				@include edgtTableCellLayout();
				vertical-align: top;
				text-align: left;
			}

			.edgt-post-title {
				margin-bottom: 7px;
			}

			.edgt-post-info-top {
				margin-bottom: 27px;
				margin-top: 0;
			}

			.edgt-post-info-bottom {
				padding-top: 23px;
				margin-top: 0;
			}


		}

		&.format-quote {

			.edgt-post-text {
				padding: 40px 42px 20px;
				background-color: #f9f9f9;
			}

			.edgt-post-text-main {
				display: table;
				table-layout: fixed;
				height: 100%;
				width: 100%;
				text-align: left;
			}

			.edgt-post-mark {
				position: absolute;
				top: 40px;
				right: 43px;
				font-size: 98px;
				color: #b9b9b9;
				opacity: 0.22;

				.edgt-quote-mark {
					padding-top: 5px;
				}
			}

			.edgt-post-link-holder {
				@include edgtTableCellLayout();
				vertical-align: top;
				text-align: left;
			}

			.edgt-post-title {
				margin-bottom: 0;
			}

			.edgt-quote-author {
				color: #b9b9b9;
				font-weight: 600;
				font-size: 14px;
				display: block;
				margin-bottom: 15px;
			}

			.edgt-post-author {
				display: inline-block;
				margin-bottom: 23px;
				font-size: 13px;
				line-height: 20px;
			}

			.edgt-post-info-top {
				margin-bottom: 25px;
			}
			.edgt-post-info-bottom {
				padding-top: 23px;
				margin-top: 0;
			}
		}
	}
}

.single-post .edgt-blog-fullwidth-featured-image-holder {
	height: 370px;
	background-size: cover;
}
/* ==========================================================================
   Blog Single style - end
   ========================================================================== */