/* Single author box styles - begin
	====================================================================== */

.edgt-author-description {
    @include edgtRelativeHolderLayout();
    padding: 0;
    background-color: #f8f7f7;
    box-sizing: border-box;
    margin-top: 9px;

    .edgt-author-description-inner {
        padding: 45px 0 29px;
        min-height: 78px; // static height of author image -- edgt-author-description-image
        clear: both;
    }

    .edgt-author-description-content {
        position: relative;
        padding: 0 35px;
    }

    .edgt-author-description-image {
        float: left;
        width: 136px;
        margin-right: 11px;
        position: relative;
        bottom: 16px;

        img {
            display: block;
            border-radius: 5em;
        }
    }

    .edgt-author-description-text-holder {
        position: relative;
        padding-left: 182px;

        .edgt-author-name {
            margin: 0;

            a {
                color: $default-heading-color;

                &:hover {
                    color: $first-main-color;
                }
            }
        }

        .edgt-author-position {
            font-size: 14px;
            margin: 3px 0;
        }

        .edgt-author-email {
            margin: 0;
        }

        .edgt-author-text {
            @include edgtRelativeHolderLayout();

            > p {
                margin: 12px 0 24px;
            }
        }

        .edgt-author-social-icons {
            @include edgtRelativeHolderLayout();

            a span,a i {
                display: inline-block;
                width: 25px;
                height: 25px;
                line-height: 25px;
                font-size: 11px;
                margin-right: 8px;
                color: #fff;
                text-align: center;
                border-radius: 50%;
            }

            a i {
                font-size: 13px;
            }

            .edgt-author-social-twitter {
                background-color: $twitter-color;
                border: 1px solid $twitter-color;
            }

            .edgt-author-social-instagram {
                background-color: $instagram-color;
                border: 1px solid $instagram-color;
            }

            .edgt-author-social-facebook {
                background-color: $facebook-color;
                border: 1px solid $facebook-color;
            }

            .edgt-author-social-pinterest {
                background-color: $pinterest-color;
                border: 1px solid $pinterest-color;
            }

            .edgt-author-social-tumblr {
                background-color: $tumblr-color;
                border: 1px solid $tumblr-color;
            }

            .edgt-author-social-googleplus {
                background-color: $googleplus-color;
                border: 1px solid $googleplus-color;
            }

            .edgt-author-social-linkedin {
                background-color: $linkedin-color;
                border: 1px solid $linkedin-color;
            }
        }
    }

    @include ipad-portrait {
        .edgt-author-description-image {
            display: block;
            width: 100%;
            margin-bottom: 30px;
        }
        .edgt-author-description-text-holder {
            display: block;
            padding: 0;
        }
    }

    @include phone-landscape {
        .edgt-author-description-content {
            padding: 0 20px;
        }
    }
}

/* Single author box styles - end
	====================================================================== */