/* ==========================================================================
   Blog List - Standard style - begin
   ========================================================================== */

.edgt-blog-holder {
	
    &.edgt-blog-standard {
	    
        article {
            margin: 0 0 71px;
            padding: 0;

            &.edgt-post-has-media {
                .edgt-post-heading {
                    margin-bottom: 34px;
                }
            }

            .edgt-post-title {
                margin: 0 0 4px;
            }

            .edgt-post-excerpt-holder {
                @include edgtRelativeHolderLayout();
                margin: 0 0 35px;
            }

            .edgt-more-link-container {
                @include edgtRelativeHolderLayout();
                margin: 20px 0 0;
            }

            .edgt-post-info-top {
                margin-bottom: 18px;
	
	            > div {
		            position: relative;
		            display: inline-block;
                    padding-right: 8px;
                    margin-right: 3px;
                    font-weight: 600;
                    font-size: 14px;
                    color: #b9b9b9;

                    a {
                        color: inherit;

                        &:hover {
                            color: #606264;
                        }
                    }

                    &:last-child {
                        padding-right: 0;
                        margin-right: 0;
                    }

                    &.edgt-post-info-category {
                        a:before {
                            content: "\e017";
                            font-family: 'ElegantIcons';
                            display: inline-block;
                            margin-right: 6px;
                            position: relative;
                            top: 1px;
                            @include edgtTransition(color .2s ease-out);
                        }

                        a:hover {
                            &:before {
                                color: #606264;
                            }
                        }
                    }

                    &.edgt-blog-like {
                        i {
                            margin-right: 4px;
                        }
                    }
	            }
            }

            .edgt-post-info-bottom {
	            padding-top: 14px;
	            border-top: 1px solid #ebebeb;

                .edgt-post-info-bottom-left,
                .edgt-post-info-bottom-right {
	                position: relative;
	                display: inline-block;
	                width: 50%;
	                float: left;
                    line-height: 27px;
                }

                .edgt-post-info-bottom-left {
                    line-height:1;

                    > div {
                        position: relative;
                        display: inline-block;
                        padding-right: 8px;
                        margin-right: 3px;
                        color: $default-text-color;

                        a {
                            display: inline-block;
                            color: #000;
                            text-transform: uppercase;
                            font-weight: 600;
                            font-size: 12px;
                            line-height: 20px;
                            margin: 0 12px 5px 4px;

                            &:first-child{
                                margin-left: 0;
                            }

                            &:hover {
                                color: $first-main-color;
                            }
                        }

                        &:after {
                            content: "/";
                            position: absolute;
                            top: 0;
                            right: -2px;
                        }

                        &:last-child {
                            padding-right: 0;
                            margin-right: 0;

                            &:after {
                                content: '';
                            }
                        }
                    }
                }

                .edgt-post-info-bottom-right {
	                
                    > div {
                        margin-left: 17px;
	                    
	                    &:first-child {
		                    margin-left: 0;
	                    }
                    }
                }

                .edgt-post-info-author {
                    display: inline-block;
	                
                    a {
                        color: #444;
	                    text-transform: capitalize;
	                    
                        &:hover {
                            color: $first-main-color;
                        }
                    }
                }

                .edgt-post-info-bottom-right {
                    text-align: right;
                }

                .edgt-post-info-comments-holder,
                .edgt-blog-like {
                    display: inline-block;

                    a {
                        display: inline-block;

                        span,
                        i {
                            vertical-align: middle;
                        }
                    }
                }

                .edgt-blog-like {
	                
                    i:first-child,
                    span:first-child {
                        margin-right: 7px;
	                    color: #c4c3c3;
	                    @include edgtTransition(color 0.15s ease-out);
                    }

                    &:hover {
	                    
                        i:first-child,
                        span:first-child {
                            color: $first-main-color;
                        }
                    }
                }

                .edgt-post-info-comments-holder {
	                
                    span:first-child {
                        margin-right: 7px;
	                    color: #c4c3c3;
	                    @include edgtTransition(color 0.15s ease-out);
                    }
	                
                    &:hover {
	                    
                        span:first-child {
                            color: $first-main-color;
                        }
                    }
                }
            }

            &.format-audio {
                &.edgt-post-no-media.has-post-thumbnail {
                    .edgt-post-heading {
                        margin-bottom: 50px;
                    }
                }
            }

            &.format-link {
                margin-bottom: 88px;
	            
                .edgt-post-text{
	                padding: 48px 32px 7px 46px;
                    overflow: hidden;
                    background-color: #f9f9f9;
                }
	            
                .edgt-post-text-main {
                    display: table;
                    table-layout: fixed;
                    height: 100%;
                    width: 100%;
                    text-align: left;
                }
	            
                .edgt-post-mark {
                    position: absolute;
                    top: 9px;
                    right: 17px;
                    font-size: 125px;
                    color: #b9b9b9;
                    opacity: 0.22;

                    .edgt-link-mark {
                        padding-top: 5px;
                    }
                }
	            
                .edgt-post-link-holder {
                    @include edgtTableCellLayout();
                    vertical-align: top;
                    text-align: left;
                }
	            
                .edgt-post-title {
                    margin-bottom: 4px;
                }

                .edgt-post-info-top {
                    margin-bottom: 29px;
                }

                .edgt-post-info-bottom {
                    padding-top: 14px;
                }
            }

            &.format-quote {
                margin-bottom: 88px;
	            
                .edgt-post-text {
	                padding: 48px 32px 7px 46px;
                    overflow: hidden;
                    background-color: #f9f9f9;
                }
	            
                .edgt-post-text-main {
                    display: table;
                    table-layout: fixed;
                    height: 100%;
                    width: 100%;
                    text-align: left;
                }
	            
                .edgt-post-mark {

                    position: absolute;
                    top: 50px;
                    right: 30px;
                    font-size: 98px;
                    color: #b9b9b9;
                    opacity: 0.22;

                    .edgt-quote-mark {
                        padding-top: 5px;
                    }
                }
	            
                .edgt-post-link-holder {
                    @include edgtTableCellLayout();
                    vertical-align: top;
                    text-align: left;
                }
	            
                .edgt-post-title {
                    margin-bottom: 9px;
                }

                .edgt-quote-author {
                    color: #b9b9b9;
                    font-weight: 600;
                    font-size: 14px;
                    display: block;
                    margin-bottom: 29px;
                    line-height:1;
                }
	            
                .edgt-post-author {
	                display: inline-block;
	                margin-bottom: 23px;
	                font-size: 13px;
	                line-height: 20px;
                }

                .edgt-post-info-top {
                    margin-bottom: 25px;
                }
                .edgt-post-info-bottom {
                    padding-top: 13px;
                }
            }
        }
    }
}
/* ==========================================================================
   Blog List - Standard style - end
   ========================================================================== */